:root{
  --size: 35px;
  --dark-color: #22223b;
  --orange: #ffbe0b;
}

body{
  height: 100dvh;
  background-color: #4a4e69;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title{
  font-size: 40px;
  text-transform: uppercase;
  color: white;
  letter-spacing: 2rem;
  margin-block-end: 20px;
}

.darck{
  color: black;
}

.App{
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.Board{
  background-color: #14213d;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 2px;
  box-shadow: 0 1px 1px rgba(246, 244, 244, 0.12), 
  0 2px 2px rgba(0,0,0,0.12), 
  0 4px 4px rgba(0,0,0,0.12), 
  0 8px 8px rgba(0,0,0,0.12),
  0 16px 16px rgba(0,0,0,0.12);
}

.on,.off{
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  border: none;
  margin: var(--size);
}
.on {
background: #ffbe0b;
box-shadow: 
0 0 var(--size) #ffbe0b,
0 0 calc(var(--size)*2 #ffbe0b)
;
}

.win{
  font-size: 30px;
  text-transform: uppercase;
  text-align: center;
  color: white;
}

.off{
  background-color: black;
}